  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  max-width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__imac {
  justify-content: center;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: flex;
}

.card__info {
  color: #060b35;
  text-align: left;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  min-height: 14px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__button {
  text-transform: uppercase;
