  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__price-text {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.card__reviews {
  justify-content: space-between;
